import React from 'react';
import ParticlesBg from 'particles-bg';

const CustomParticlesBackground = () => {
  const config = {
    num: [1, 2], // Liczba czsteczek
    rps: 0.001,       // Obrt na sekund
    radius: [1, 5],// Promie czsteczek
    life: [1, 3], // Czas ycia czsteczek w sekundach
    v: [0.0001, 0.0002],    // Prdko czsteczek
    tha: [-120, 120], // Kt ruchu czsteczek
    alpha: [1, 0.2],// Przezroczysto czsteczek
    scale: [0.5, 0.8],// Skalowanie czsteczek
    position: 'all',// Pozycja generowania czsteczek
    color: ['#ff7e5f', '#feb47b'], // Kolory czsteczek
    cross: 'dead',  // Sposb zachowania na krawdziach
    random: 5,     // Stopie losowoci
    g: 3,           // Grawitacja
  };

  return (
    <ParticlesBg type="custom" config={config} bg={true} />
  );
};

export default CustomParticlesBackground;